import { NoteBanner } from "../note-banner";

export function LocalizedContentNote({
  isActive,
  locale,
}: {
  isActive: boolean;
  locale: string;
}) {
  const activeLocaleNoteContent = {
    "en-US": {
      linkText:
        "This page was translated from English by the community. Learn more and join the MDN Web Docs community.",
      url: "/en-US/docs/MDN/Contribute/Localize#active_locales",
    },
    fr: {
      linkText:
        "Cette page a t traduite  partir de l'anglais par la communaut. Vous pouvez galement contribuer en rejoignant la communaut francophone sur MDN Web Docs.",
      url: "/fr/docs/MDN/Contribute/Localize#langues_actives",
    },
    ja: {
      linkText:
        "MDN Web Docs ",
      url: "/ja/docs/MDN/Contribute/Localize#active_locales",
    },
    ko: {
      linkText:
        "     . MDN Web Docs     .",
    },
    ru: {
      linkText:
        "        .      ,     MDN Web Docs.",
    },
    "zh-CN": {
      linkText:
        " MDN Web Docs ",
      url: "/zh-CN/docs/MDN/Contribute/Localize#",
    },
  };
  const inactiveLocaleNoteContent = {
    de: {
      linkText:
        "Der Inhalt dieser Seite wurde von der Community bersetzt, jedoch wird er nicht mehr aktiv gepflegt und kann daher veraltet sein. Wenn du mithelfen mchtest, kannst du hier herausfinden wie deaktivierte bersetzungen reaktiviert werden knnen.",
    },
    "en-US": {
      linkText:
        "This page was translated from English by the community, but it's not maintained and may be out-of-date. To help maintain it, learn how to activate locales.",
    },
    es: {
      linkText:
        "Esta pgina fue traducida del ingls por la comunidad, pero no se mantiene activamente, por lo que puede estar desactualizada. Si desea ayudar a mantenerlo, descubra cmo activar las configuraciones regionales inactivas.",
    },
  };

  const linkText = isActive
    ? (activeLocaleNoteContent[locale] &&
        activeLocaleNoteContent[locale].linkText) ||
      activeLocaleNoteContent["en-US"].linkText
    : (inactiveLocaleNoteContent[locale] &&
        inactiveLocaleNoteContent[locale].linkText) ||
      inactiveLocaleNoteContent["en-US"].linkText;
  const url = isActive
    ? (activeLocaleNoteContent[locale] &&
        activeLocaleNoteContent[locale].url) ||
      activeLocaleNoteContent["en-US"].url
    : "https://github.com/mdn/translated-content/blob/main/PEERS_GUIDELINES.md#activating-a-locale";

  const type = isActive ? "neutral" : "warning";
  return <NoteBanner linkText={linkText} url={url} type={type} />;
}
